import React from "react"
import { graphql } from "gatsby"
import styled from "styled-components"

import "../components/layout.css"

const Information = styled.div`
  background-color: rgba(206, 174, 142);
  font-family: 'Calibre-Regular';
  color: white;
  height: 50vh;
  position: relative;
  z-index: 999;
`
const Title = styled.h1`
  font-size: 48px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
`

const Text = styled.p`
  font-size: 24px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
  max-width: 800px;
`

const Image = styled.div`

  img {
    margin: auto;
    display: block;
  }
`

const Page = ({ data: { prismicPage } }) => {

  const { data } = prismicPage;

  var itemsPre = function(prismicPage) {
    this.name = prismicPage.data.title.text;
    this.tags = [];
    prismicPage.tags.map(tag => {this.tags.push(tag)});
  }

  var items = new itemsPre(prismicPage);

  console.log(items);
  console.log(data.image_gallery);
  

  return (
    <>
      {data.image_gallery.map((image, index) => (
        <Image key={index}>
          <img src={image.image.url}/>
        </Image>
        )
      )}
      <Information>
        <Title>{items.name}</Title>
        <Text>{data.text.text}</Text>
      </Information>
    </>
  )
}

export default Page

export const pageQuery = graphql`
  query PostBySlug($uid: String!) {
    prismicPage(uid: { eq: $uid }) {
      tags
      uid
      data {
        title {
          text
        }
        text {
          text
        }
        image_gallery {
          image {
            alt
            copyright
            url
          }
        }
      }
    }
  }
`
